
















































import { Component, Vue, Prop, Emit } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
const pageTemplate = namespace('pageTemplate');
const collapse = namespace('collapse');

@Component
export default class LeftToolbar extends Vue {
  @Emit('onDragStart')
  handleDragStart(obj: any) {}
  @Emit('tempateNameChange')
  tempateNameChange(newName: string) {}
  @Emit('handleSideBarOpen')
  handleSideBarOpen(direction) {}

  @Prop() templateName: string;
  @Prop() narrow: boolean;
  @Prop() leftBarOpen;

  @pageTemplate.State((state) => state.editFlag) editFlag: Array<any>;
  @pageTemplate.Mutation('UPDATE_EDIT_FLAG') handleEditFlagChange;

  private pageElement: Array<any> = [
    {
      title: '表格',
      element: [
        {
          label: '表格',
          value: 'table',
          icon: 'table-icon',
          allowType: 0
        }
      ]
    },
    {
      title: '统计图',
      element: [
        {
          label: '饼图',
          value: 'pie',
          icon: 'pie-icon',
          allowType: 0
        },
        {
          label: '柱状图',
          value: 'bar',
          icon: 'bar-icon',
          allowType: 0
        },
        {
          label: '折线图',
          value: 'line',
          icon: 'line-icon',
          allowType: 0
        },
        {
          label: '监控图表',
          value: 'monitorChart',
          icon: 'line-icon',
          allowType: 0
        },
        {
          label: '监控节点',
          value: 'monitorSteps',
          icon: 'line-icon',
          allowType: 0
        }
      ]
    },
    {
      title: '表单',
      element: [
        {
          label: '表单',
          value: 'singleForm',
          icon: 'single-form',
          allowType: 0
        }
        // {
        //   label: '双列表单',
        //   value: 'doubleForm',
        //   icon: 'double-icon',
        //   allowType: 0
        // }
      ]
    },
    {
      title: '表单元素组',
      element: [
        {
          label: '文本框',
          value: 'text',
          icon: 'input-icon',
          allowType: 1
        },
        {
          label: '文本域',
          value: 'textarea',
          icon: 'text-icon',
          allowType: 1
        },
        {
          label: '下拉框',
          value: 'select',
          icon: 'select-icon',
          allowType: 1
        },
        {
          label: '日期选择器',
          value: 'date',
          icon: 'datepicker-icon',
          allowType: 1
        },
        {
          label: '时间选择器',
