          allowType: 1
        },
        {
          label: '文件上传',
          value: 'upload',
          icon: 'upload-icon',
          allowType: 1
        },
        {
          label: '按钮',
          value: 'button',
          icon: 'button-icon',
          allowType: 1
        },
        {
          label: '开关',
          value: 'switch',
          icon: 'form-tool-switch',
          allowType: 1
        },
        {
          label: '提示卡',
          value: 'alert',
