












































































































































































































































































































































import { Component, Vue } from 'vue-property-decorator';
import { CanvasContainer } from './taskDetailConfig/utils/CanvasContainer';
import { dataViewQuery } from '@api/viewData';
import {
  savePageTemplate,
  pageTemplateDetail,
  addLayoutTemplate
} from '@api/pageTemplate';
import formDisplay from '@views/processConfig/customForm/compoents/formDisplay.vue';
import TemplateBlock from './taskDetailConfig/components/templateBlock.vue';
import { dataViewFields } from '@api/viewData';
import { changeBlockFields, getGridROWS } from './taskDetailConfig/utils/util';
import { arrayFind } from '@utils/util';
import Base64 from '@utils/Base64';
import { cloneDeep } from 'lodash';
import pinyin4js from 'pinyin4js';
import PreviewTemplate from './taskDetailConfig/components/previewTemplate.vue';
import LayoutTemplate from './taskDetailConfig/components/layoutTemplate.vue';
import LeftToolbar from './taskDetailConfig/components/leftToolbar.vue';
import RightToolbar from './taskDetailConfig/components/rightToolbar.vue';
import { namespace } from 'vuex-class';

const pageTemplate = namespace('pageTemplate');
const taskLibraryManage = namespace('taskLibraryManage');
let containerProxy: any = null;
interface FormAttribute {
  labelPosition: string;
  labelSuffix: string;
  labelWidth: string;
  blockName: string;
  showBlockName: boolean;
  formList: Array<any>;
}
interface ExtendSet {
  extendColName: string;
  addDataSetVal: string;
  addDataQueryList: Array<any>;
  addDataValue: Array<any>;
  addSetType: string;
  tabList: Array<any>;
  addViewDataLists: Array<any>;
  colsAdd: Array<any>;
}
interface DefaultValueBind {
  cfgType: string;
  bindType: string;
  bindTarget: string;
  bindField: string;
  bindExpress: any;
  targetType: string;
  valueShow: string;
  parameterList: Array<any>;
}

interface ShowConfig {
  showType: string;
  valueShow: string;
  targetType: string;
  bindField: string;
  symbolShow: string;
  inputValue: string;
  parameterList: Array<any>;
  relationship: string;
}

interface ItemAttribute {
  sourceDataType: string;
  type: string;
  label: string;
  key: string;
  placeHolder: string;
  value: string;
  span: number;
  itemMultiple: boolean;
  itemMultipleLimit: number;
  clearAble: boolean;
  disabled: boolean;
  required: boolean;
  cols: Array<any>;
  dataLabel: string;
  dataValue: string;
  dataSetVal: string;
  dataQueryList: Array<any>;
  colsList: Array<any>;
  editable: boolean;
  readable: boolean;
  recordable: boolean;
  options: Array<any>;
  rules: Array<any>;
  associatedNodeList: Array<any>;
  bindValue: DateSetFrom;
  addSetList: Array<ExtendSet>;
  flowVisibleCode: string;
  addLine: boolean;
  performResult: string;
  parameterList: Array<any>;
  dictFlagValue: number;
  defaultValue: DefaultValueBind | null;
  showConfig: ShowConfig | null;
  remoteDataUrl: string;
  remoteDataLabel: string;
  remoteDataValue: string;
  remoteDataType: string;
  centerFont: boolean;
  description: string;
  alertType: string;
  showIcon: boolean;
  mutexSetList: Array<ExtendSet>;
  origId: string;
  filterText: Array<FilterText>;
}
interface FilterText {
  filterType: string;
  textLinkMethod: string;
  textLink: string;
  textLinkValue: string;
}
interface DateSetFrom {
  dateValue: string;
  dateSpecify: string;
  systemDate: string;
  variableDate: string;
  selectDate: string;
  countDate: string;
  numDate: number;
  predestination: string;
}
@Component({
  components: {
    TemplateBlock,
    formDisplay,
    PreviewTemplate,
    dataViewFields,
    LayoutTemplate,
    LeftToolbar,
    RightToolbar
  }
})
export default class EditPageTemplate extends Vue {
  @pageTemplate.State((state) => state.layoutList) layoutList!: Array<any>;
  @pageTemplate.State((state) => state.dataSetList) dataSetList;
  @pageTemplate.State((state) => state.activeBlock) activeBlock;
  @pageTemplate.State((state) => state.editFlag) editFlag;
  @pageTemplate.Action('getLayoutTemplateList') getLayoutTemplateList;
  @pageTemplate.Action('getDataSet') getDataSets;
  @pageTemplate.Mutation('UPDATE_ACTIVE_BLOCK') updateActiveBlock;
  @pageTemplate.Mutation('UPDATE_EDIT_FLAG') handleEditFlagChange;

  @taskLibraryManage.State((state) => state.taskForm) taskForm;
  @taskLibraryManage.Mutation('UPDATETASKFORM') updateTaskForm;
  @taskLibraryManage.State('taskLib') taskLib; //用于新增后保存后再次进入页面判断

  private draggingType = ''; // 正在拖拽的元素的类型：icon(左侧工具栏)、block(整个块)、dataset(右侧数据集的字段)
  private iconType = ''; // 从左侧工具栏拖入块中的类型(表格、饼图...)
  private allowType: any = null; // 表单项一个块支持拖入多个，其他类型不可以
  private grid: any = []; // 记录整个页面模板的信息
  private ROWS = 3; // 模板行数
  private COLUMNS = 3; // 模板列数
  private templateName = ''; //模板名称
  private leftBarOpen = true; // 左侧工具栏展开状态
  private rightBarOpen = true; // 右侧工具栏展开状态
  private loading = false;
  private draggingArea = '';
  private clickRandom = 0; // 用来标记点击了内容块，为了：点击内容块 -> 切换数据集但不做其他操作 -> 再次点击该块，让数据集切换为原来的选择
  private multipleData: any = {}; // 存放多个数据集的真实数据
  private dragNodeData: any = null; // 拖拽的数据集字段node
  private activeFormItem: any = {}; // 当前操作的表单项
  private draggingAreaType = '';
  private optionLabel: Array<any> = [];
  private dataSet: Array<any> = [];
  private viewDataSet: Array<any> = [];
  private formAttribute: FormAttribute = {
    labelPosition: 'right',
    labelSuffix: '：',
    labelWidth: '120',
    blockName:
      '内容块' +
      Math.random()
        .toString(36)
        .substring(2),
    showBlockName: false,
    formList: []
  };
  private itemAttribute: ItemAttribute = {
    sourceDataType: '',
    type: '',
    label: '',
    key: '',
    placeHolder: '',
    value: '',
    span: 24,
    itemMultiple: false,
    itemMultipleLimit: 1,
    clearAble: false,
    disabled: false,
    required: false,
    options: [],
    parameterList: [],
    performResult: '',
    cols: [],
    dataLabel: '',
    dataValue: '',
    dataSetVal: '',
    dataQueryList: [],
    colsList: [],
    associatedNodeList: [],
    // filterType: 'form',
    // textLinkMethod: '1',
    // textLink: '',
    // textLinkValue: '',
    alertType: '',
    centerFont: false,
    description: '',
    showIcon: false,
    editable: false,
    readable: false,
    recordable: false,
    rules: [
      {
        required: false,
        message: ''
      }
    ],
    bindValue: {
      dateValue: '',
      dateSpecify: '',
      systemDate: '',
      variableDate: '',
      selectDate: '',
      countDate: '',
      numDate: 0,
      predestination: ''
    },
    addLine: false,
    addSetList: [],
    flowVisibleCode: '',
    dictFlagValue: 1,
    defaultValue: null,
    remoteDataUrl: '',
    remoteDataLabel: '',
    remoteDataValue: '',
    showConfig: null,
    remoteDataType: 'get',
    mutexSetList: [],
    origId: '',
    filterText: []
  };
  private previewDialogVisible = false;

  get currentBlock() {
    return cloneDeep(this.activeBlock);
  }

  // 当前页面中所有表单项的列表，除button和upload之外
  get formItemList() {
    const formItemList: Array<any> = [];
    this.grid.map((item) => {
      if (item && item.goods && item.goods.formAttribute) {
        const formList = item.goods.formAttribute.formList;
        formList.map((innerItem) => {
          if (
            innerItem.formItemType !== 'button' &&
            innerItem.formItemType !== 'upload'
          ) {
            formItemList.push(JSON.parse(JSON.stringify(innerItem)));
          }
        });
      }
    });
    return formItemList;
  }

  // 当前页面中所有被设置为视图参数的表单项
  get beRelatedFromItemList() {
    const beRelatedFromItemList: Array<any> = [];
    this.grid.map((item) => {
      if (item && item.goods && item.goods.queryParams) {
        const formList = item.goods.queryParams.filter(
          (innerItem) => innerItem.valueBy === '2'
        );
        formList.map((i) => {
          if (beRelatedFromItemList.indexOf(i.relationFormItem) === -1) {
            beRelatedFromItemList.push(i.relationFormItem);
          }
        });
      }
    });
    return beRelatedFromItemList;
  }

  get chartList() {
    const chartList: Array<any> = [];
    this.grid.map((item) => {
      if (item && item.goods) {
        if (item.goods.blockName) {
          const obj: any = {
            value: item.id,
            label: item.goods.blockName
          };
          switch (item.goods.type) {
            case 'pie':
              obj.options = [
                {
                  value: 'label',
                  label: 'label'
                },
                {
                  value: 'value',
                  label: 'value'
                }
              ];
              break;
            case 'bar':
            case 'line':
              obj.options = [
                {
                  value: 'x',
                  label: 'x轴'
                },
                {
                  value: 'y',
                  label: 'y轴'
                }
              ];
              break;
            case 'table':
              obj.options = [
                {
                  value: 'cell',
                  label: '单元格'
                }
              ];
              break;
            default:
          }
          chartList.push(obj);
        }
      }
    });
    return chartList;
  }

  // 拖Icon入block
  handleDragStart(type: any) {
    this.draggingType = 'icon';
    this.draggingArea = 'draggingTooles';
    this.iconType = type.iconType;
    this.allowType = type.allowType;
  }

  // 顶部工具栏button
  getOperateButtons() {
    return [
      {
        label: '保存',
        icon: 'el-icon-plus',
        className: '',
        click: this.saveTemplate
      },
      {
        label: '新增行',
        icon: 'el-icon-plus',
        className: '',
        click: this.addRow
      },
      {
        label: '预览',
        icon: 'el-icon-plus',
        className: '',
        click: this.previewTemplate
      },
      {
        label: '关闭',
        icon: '',
        className: 'close',
        click: this.close
      }
    ];
  }

  // 新增行
  addRow() {
    containerProxy.addRow();
    this.refreshGrid();
    this.ROWS++;
  }

  // 保存模板校验
  validateSaveTemplate() {
    const blocks: any = [];
    this.grid.map((item: any) => {
      if (!item.empty) {
        blocks.push({
          ...item.goods
        });
      }
    });

    // if (!this.templateName) {
    //   return '请填写模板名称';
    // }

    const illegalParams: any = [];
    for (let i = 0, len = blocks.length; i < len; i++) {
      if (!blocks[i].blockName) {
        return '块标题不能为空';
      }
      const defaultValueParams =
        (blocks[i].queryParams &&
          blocks[i].queryParams.filter((item) => item.valueBy === '0')) ||
        []; // 视图参数取值方式为使用默认值且该参数为必填时，必须填写默认值
      defaultValueParams.map((item: any) => {
        if (item.required === 0 && !item.value) {
          illegalParams.push(item.name);
        }
      });
    }

    if (illegalParams.length) {
      return `请检查视图参数设置：${illegalParams.join('、')}为必填参数`;
    } else {
      return false;
    }
  }

  // 保存模板
  saveTemplate() {
    const style = cloneDeep(this.grid);
    const blocks: Array<any> = [];
    for (let i = 0; i < style.length; i++) {
      if (style[i].goods && style[i].goods.type === 'doubleForm') {
        style[i].goods.blockName = style[i].goods.formAttribute.blockName;
      }
      const obj: any = {
        blockId: `${new Date().getTime()}${Math.floor(Math.random() * 900) +
          100}`,
        ...style[i].goods
      };
      // 参数获取方式不为使用默认值时，不需要将value提交，否则回显时在块、表单项中会有默认值
      if (obj.queryParams && obj.queryParams.length) {
        obj.queryParams.forEach((item) => {
          if (item.valueBy !== '0') {
            item.value = null;
          }
        });
      }

      blocks.push(obj);
      style[i].goods = obj.blockId;
    }

    // 保存前校验
    const passError = this.validateSaveTemplate();
    if (passError) {
      this.$message.warning(passError);
      return;
    }

    const param: any = {
      // name: this.templateName,
      blocks,
      style: Base64.encode(JSON.stringify(style))
    };
    // if (this.$route.query.type === 'modify' && this.$route.query.id) {
    //   param.id = this.$route.query.id;
    // }
    this.updateTaskForm(param);

    this.$router.push({
      path: '/taskConfigCenter/addTasks',
      query: { ...this.$route.query, isBack: 'true' }
    });

    // savePageTemplate(param).then(({ error }: { error: any }) => {
    //   this.loading = false;
    //   if (!error) {
    //     this.$confirm(param.id ? '修改成功' : '保存成功', '提示', {
    //       confirmButtonText: '返回',
    //       cancelButtonText: '继续编辑',
    //       type: 'success'
    //     })
    //       .then(() => {
    //         this.$router.push({ name: 'pageTemplate' });
    //       })
    //       .catch(() => {
    //         this.handleEditFlagChange(false);
    //       });
    //   } else {
    //     this.$message.error(error.message);
    //   }
    // });
  }

  // 模板预览
  previewTemplate() {
    this.previewDialogVisible = true;
    this.$nextTick(() => {
      (this.$refs.previewTemplate as any).initPreviewTemplate();
    });
  }

  // 关闭当前页面
  close() {
    if (this.editFlag) {
      this.$confirm('操作没有保存，确定要离开吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.$router.push({
            path: '/taskConfigCenter/addTasks',
            query: { ...this.$route.query, isBack: 'true' }
          });
        })
        .catch(() => {});
    } else {
      this.$router.push({
        path: '/taskConfigCenter/addTasks',
        query: { ...this.$route.query, isBack: 'true' }
      });
    }
  }

  // 更新当前块信息
  switchActiveBlock(block: any, trigger?: any) {
    console.log('block', block);
    if (block && block.goods && block.goods.formAttribute) {
      this.formAttribute = block.goods.formAttribute;
    }
    this.updateActiveBlock(cloneDeep(block));
    if (trigger === 'click' && block.goods && block.goods.dataSetId) {
      this.clickRandom = Math.random();
    }
  }

  // 拖拽过程中鼠标刚进入该块时触发的事件
  handleOriginEnter(e: any, block: any) {
    e.preventDefault();
    if (this.draggingType === 'block') {
      const verification = containerProxy.checkDragAllow(block);
      if (verification) {
        block.canDrop = verification;
      }
    } else if (this.draggingType === 'icon') {
      if (this.allowType === 1) {
        // 正在拖拽的是表单项
        if (
          block.goods &&
          block.goods.type &&
          (block.goods.type === 'singleForm' ||
            block.goods.type === 'doubleForm')
        ) {
          block.canDrop = 1;
        } else {
          block.canDrop = 2;
        }
      } else {
        const verification = containerProxy.checkIconDrag(block);
        block.canDrop = verification;
      }
    }
  }

  // 拖拽过程中鼠标离开该块时触发的事件
  handleOriginLeave(block: any) {
    block.canDrop = 0;
  }

  // 移动块的位置
  handleOriginStart(block: any) {
    this.draggingType = 'block';
    containerProxy.setDraggingBlock(block);
  }

  // Icon，数据集或源块放入模块中的操作处理
  handleSomethingDrop(block: any) {
    if (this.draggingType === 'block') {
      // 判断是否允许放下，如果允许，则触发块位置变动
      if (containerProxy.checkDragAllow(block) === 1) {
        containerProxy.moving(block);
        this.refreshGrid();
      }
    } else if (this.draggingType === 'icon') {
      if (this.allowType === 0) {
        // 拖拽的是除表单项之外的其他页面元素
        if (block.empty) {
          block.empty = false;
          block.goods = {
            type: this.iconType,
            blockName:
              '内容块' +
              Math.random()
                .toString(36)
                .substring(2),
            showBlockName: false
          };
          if (
            this.iconType === 'singleForm' ||
            this.iconType === 'doubleForm'
          ) {
            const obj = {
              labelPosition: 'right',
              labelSuffix: ':',
              labelWidth: '120',
              blockName:
                '内容块' +
                Math.random()
                  .toString(36)
                  .substring(2),
              showBlockName: false,
              formList: []
            };
            block.empty = true;
            this.formAttribute = obj;
            this.$set(block.goods, 'formAttribute', obj);
          }
          if (this.iconType === 'monitorChart') {
            block.goods.monitorColor = '#409eff';
          }
          containerProxy.setDraggingBlockContent(block);
          this.refreshGrid();
        }
      } else {
        if (this.draggingArea === 'draggingTooles') {
          this.generateFormItem('single', 24, '', block);
        }
        // this.setFormItem(this.iconType, block);
      }
    } else if (this.draggingType === 'dataSet') {
      if (!block.goods || !block.goods.type) {
        this.$message.warning('请先从左侧工具栏拖入页面元素');
        return;
      }
      const dragNode = this.dragNodeData;
      const dataSetId = dragNode.isLeaf
        ? dragNode.parent.data.id
        : dragNode.data.id;
      if (block.goods.type === 'table') {
        this.drawCanvas(block, dataSetId, dragNode);
      } else if (
        block.goods.type === 'singleForm' ||
        block.goods.type === 'doubleForm'
      ) {
        const optionsList: Array<any> = [];
        dragNode.data.fields.forEach((item: any) => {
          const obj = {
            label: item.name,
            value: item.fieldCode
          };
          optionsList.push(obj);
        });
        block.goods.formAttribute.formList.forEach((formItem: any) => {
          if (formItem.key === this.activeFormItem.key) {
            this.$set(formItem, 'dataSetId', dataSetId);
            this.$set(formItem, 'options', optionsList);
          }
        });
      }
    }
    block.canDrop = 0;
    this.switchActiveBlock(block);
  }
  // 鼠标点击当前表单项
  clickActiveItem(item: any) {
    console.log('item', item);
    // if (this.grid.length > 0) {
    //   this.grid.forEach((blockItem) => {
    //     if (blockItem.goods.type && blockItem.goods.type === 'singleForm') {
    //       this.switchActiveBlock(blockItem, 'click');
    //     }
    //   });
    // }
    this.itemAttribute = item;
    if (!(this.itemAttribute.filterText instanceof Array)) {
      this.$set(this.itemAttribute, 'filterText', []);
    }
    this.optionLabel = arrayFind(this.dataSet, 'id', item.dataSetVal)
      ? arrayFind(this.dataSet, 'id', item.dataSetVal)['fields']
      : [];
    if (this.optionLabel.length > 0) {
      const theSet = cloneDeep(this.optionLabel);
      if (this.itemAttribute.type === 'select') {
        this.itemAttribute.cols = theSet.map((col) => {
          return {
            ...col,
            prop: col.fieldCode,
            label: col.fieldName
          };
        });
      }
    }

    this.stopBubble('sourceDataType');
  }
  stopBubble(e) {
    // 如果提供了事件对象，则这是一个非IE浏览器
    if (e && e.stopPropagation) {
      e.stopPropagation();
    }
    // 因此它支持W3C的stopPropagation()方法
    // e.stopPropagation();
    // 否则，我们需要使用IE的方式来取消事件冒泡
    else {
      const a: any = window.event;
      if (a !== null) a.cancelBubble = true;
    }
  }
  draggingAreaChange(msg) {
    this.draggingArea = msg;
  }
  draggingAreaTypeChange(msg) {
    this.draggingAreaType = msg;
  }
  // 拖拽表单项数据处理
  generateFormItem(type, val, index, block) {
    if (
      block.goods &&
      block.goods.type &&
      (block.goods.type === 'singleForm' || block.goods.type === 'doubleForm')
    ) {
      let formItem: any = {};
      const keyRandom = 'yejijizhunchae';
      formItem = {
        sourceDataType: 'static',
        type: this.iconType,
        key: keyRandom,
        placeHolder: '请输入',
        value: '',
        span: val,
        editable: false,
        readable: false,
        recordable: false,
        clearAble: true,
        disabled: false,
        required: false,
        rules: [
          {
            required: false,
            message: ''
          }
        ],
        dataQueryList: [],
        colsList: [],
        performResult: '',
        parameterList: [],
        substanceText: '',
        addSetList: [],
        mutexSetList: [],
        origId: '',
        bindValue: {},
        addLine: false,
        remoteDataUrl: '',
        remoteDataLabel: '',
        remoteDataValue: '',
        remoteDataType: 'get',
        filterText: []
      };
      // this.sourceDataType = 'static';
      if (this.iconType === 'text') {
        formItem.label = '单行文本';
        // formItem.textLink = '';
        // formItem.textLinkValue = '';
        formItem.sourceDataType = 'dataView';
      } else if (this.iconType === 'number') {
        formItem.label = '数字';
        formItem.placeHolder = '请输入数字';
        // formItem.textLink = '';
        // formItem.textLinkValue = '';
        formItem.sourceDataType = 'dataView';
      } else if (this.iconType === 'textarea') {
        formItem.label = '多行文本';
        formItem.sourceDataType = 'dataView';
      } else if (this.iconType === 'table') {
        formItem.label = '表格';
        formItem.cols = [];
        formItem.value = [];
        formItem.datas = [];
        formItem.associatedNodeList = [];
        formItem.sourceDataType = 'dataView';
        formItem.dictFlagValue = 1;
      } else if (this.iconType === 'date') {
        formItem.label = '日期';
        formItem.bindValue = {
          dateValue: '',
          dateSpecify: '',
          systemDate: '',
          variableDate: '',
          selectDate: '',
          countDate: '',
          numDate: 0,
          predestination: ''
        };
        formItem.sourceDataType = 'dataView';
      } else if (this.iconType === 'select') {
        formItem.label = '下拉选择器';
        formItem.value = [];
        formItem.options = [];
        formItem.itemMultiple = false;
        formItem.itemMultipleLimit = 1;
        formItem.associatedNodeList = [];
      } else if (this.iconType === 'cascader') {
        formItem.label = '级联选择器';
        formItem.options = [];
        formItem.value = [];
        formItem.associatedNodeList = [];
      } else if (this.iconType === 'radioGroup') {
        formItem.label = '单选框组';
        formItem.associatedNodeList = [];
        formItem.options = [];
      } else if (this.iconType === 'checkboxGroup') {
        formItem.label = '多选框组';
        formItem.options = [];
        formItem.value = [];
        formItem.associatedNodeList = [];
      } else if (this.iconType === 'inputNumber') {
        formItem.label = '计数器';
        formItem.bindValue = {
          countDate: '',
          numDate: ''
        };
      } else if (this.iconType === 'switch') {
        formItem.label = '开关';
        formItem.value = false;
      } else if (this.iconType === 'ueditor') {
        formItem.label = '富文本编辑器';
      } else if (this.iconType === 'upload') {
        formItem.label = '上传';
        formItem.btnProp = {
          buttonText: '文件上传'
        };
        formItem.value = [];
      } else if (this.iconType === 'alert') {
        formItem.label = '提示卡';
        formItem.value = '';
        formItem.alertType = 'success';
        formItem.description = '';
        formItem.centerFont = false;
        formItem.showIcon = true;
      } else if (this.iconType === 'hidden') {
        formItem.label = '隐藏项';
        formItem.value = '';
      } else if (this.iconType === 'button') {
        formItem.label = '按钮';
        formItem.value = '确定';
        formItem.btnProp = {
          buttonText: '确定'
        };
      } else if (this.iconType === 'time') {
        formItem.label = '时间';
        formItem.value = '';
      }

      if (type === 'single') {
        this.formAttribute.formList.push(formItem);
      }

      this.draggingType = '';
      if (formItem.key && formItem.key !== '') {
        this.itemAttribute = formItem;
      }
      const AttributeList: Array<any> = [];
      this.formAttribute.formList.forEach((item) => {
        if (item.key === formItem.key) {
          AttributeList.push(item);
        }
        if (AttributeList.length > 1) {
          const sa = Math.floor(Math.random() * 90) + 10;
          formItem.key = pinyin4js
            .convertToPinyinString(formItem.label, '', pinyin4js.WITHOUT_TONE)
            .concat(sa.toString());
        } else {
          formItem.key = pinyin4js.convertToPinyinString(
            formItem.label,
            '',
            pinyin4js.WITHOUT_TONE
          );
        }
      });
      block.goods.formAttribute = this.formAttribute;
      containerProxy.setDraggingBlockContent(block);
      this.refreshGrid();
    } else {
      this.$message.warning('请将表单元素拖入到表单内');
    }
  }
  getDataSet() {
    dataViewFields({}).then(({ data, error }) => {
      if (data) {
        this.dataSet = data;
        this.viewDataSet = data;
      } else {
        this.$message.error(error.message);
      }
    });
  }
  // 将字段拖入饼图、柱状图和折线图时
  handleChartFieldsDrop(block: any, type: any) {
    if (this.draggingType === 'dataSet') {
      const dragNode = this.dragNodeData;
      let dataSetId = '';
      if (!dragNode.isLeaf) {
        dataSetId = dragNode.data.id;
        this.$message.error('请拖入单一字段');
      } else {
        dataSetId = dragNode.parent.data.id;
        dragNode.data.attrFlag = type;
      }
      this.drawCanvas(block, dataSetId, dragNode);
    }
    this.switchActiveBlock(block);
  }

  // 设置内容块中数据源的实际数据
  drawCanvas(block: any, dataSetId: any, dragNode: any) {
    if (!block.goods.dataSetId) {
      // 初始化block的数据源配置
      block.goods.dataSetId = dataSetId;
    } else if (dataSetId !== block.goods.dataSetId) {
      // 判断当前拖入的字段与块中原有字段是否属于同一个数据集
      this.$message.warning('字段必须来自同一个数据集！');
      return;
    }

    const dataset = cloneDeep(arrayFind(this.dataSetList, 'id', dataSetId));
    const queryParams = dataset.queryParams
      ? dataset.queryParams.filter((item: any) => item.required !== 3)
      : [];
    block.goods.queryParams = queryParams.map((item: any) => ({
      ...item,
      valueBy: '0',
      value: item.defaultValue || null,
      relationFormItem: '', // 关联的表单项
      relationChart: '', // 关联的图表
      relationChartField: '' // 关联的图表字段
    }));

    // 设置当前块的字段属性
    changeBlockFields(dragNode, block);
    this.getRealContentById(block);

    // 刷新容器中的block配置
    containerProxy.setDraggingBlockContent(block);
    this.refreshGrid();
  }

  // 详细数据
  getRealContentById(block: any) {
    if (block.goods && block.goods.type !== 'monitorSteps') {
      if (!block.goods || !block.goods.dataSetId) {
        return;
      }
      const param: any = { viewId: block.goods.dataSetId };
      const queryParams: any = [];
      if (block.goods.type !== 'monitorChart') {
        for (let i = 0, len = block.goods.queryParams.length; i < len; i++) {
          const queryParam = block.goods.queryParams[i];
          if (queryParam.required === 0 && !queryParam.value) {
            // 必选参数为空时不做查询
            this.$message.warning(
              `${block.goods.blockName}中数据源必选参数为空！`
            );
            // this.$set(this.multipleData, block.id, []);
            return;
          }
          queryParams.push({
            code: queryParam.code,
            type: queryParam.type,
            value: queryParam.value
          });
        }
      } else {
        if (block.goods.queryParams) {
          block.goods.queryParams.forEach((item) => {
            if (item.code === 'monitorName') {
              item.value = block.goods.monitorName;
            }
            queryParams.push({
              code: item.code,
              type: item.type,
              value: item.value
            });
          });
        }
      }
      param.queryParams = queryParams;
      dataViewQuery(param).then(
        ({ data, error }: { data: any; error: any }) => {
          if (!error) {
            if (block.goods.type === 'table') {
              data.rows.forEach((item: any) => {
                item.isEdit = true;
              });
            }
            if (block.goods.type === 'monitorSteps') {
              const list = cloneDeep(this.multipleData);
              if (this.multipleData[block.id]) {
                list[block.id][block.goods.currentStep] = data.rows;
                this.multipleData = list;
              } else {
                list[block.id] = [];
                list[block.id][block.goods.currentStep] = data.rows;
                this.multipleData = list;
              }
            } else {
              this.$set(this.multipleData, block.id, data.rows);
            }
          } else {
            this.$message.error(error.message || '获取数据失败');
          }
        }
      );
    } else {
      const list = block.goods.stepsList;
      list.forEach((item: any, index: any) => {
        if (item.dataSetId) {
          const queryParam: any = { viewId: item.dataSetId };
          const preview: any = [];
          if (block.goods.queryParams) {
            block.goods.queryParams.forEach((item) => {
              if (item.code === 'monitorName') {
                item.value = block.goods.queryName;
              }
              preview.push({
                code: item.code,
                type: item.type,
                value: item.value
              });
            });
          }
          queryParam.queryParams = preview;
          dataViewQuery(queryParam).then(
            ({ data, error }: { data: any; error: any }) => {
              if (!error) {
                const dataList = cloneDeep(this.multipleData);
                if (this.multipleData[block.id]) {
                  dataList[block.id][index] = data.rows;
                  this.multipleData = dataList;
                } else {
                  dataList[block.id] = [];
                  dataList[block.id][index] = data.rows;
                  this.multipleData = dataList;
                }
              } else {
                this.$message.error(error.message || '获取数据失败');
              }
            }
          );
        }
      });
    }
  }

  // 点击叉号清空模块内容
  emptyContent(block: any) {
    console.log(block);
    this.$confirm('是否确认删除?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    })
      .then(() => {
        if (
          (block.goods && block.goods.type === 'singleForm') ||
          (block.goods && block.goods.type === 'doubleForm')
        ) {
          if (block.goods.formAttribute && block.goods.formAttribute.formList) {
            block.goods.formAttribute.formList = [];
          }
        }
        this.$set(this.multipleData, block.id, []);
        this.updateActiveBlock({});
        containerProxy.emptyBlock(block);
        this.refreshGrid();
      })
      .catch(() => {});
  }

  // 横向扩展块
  handleExtendWidth(block: any, direction: any) {
    containerProxy.blockWidthExtend(block, direction);
    const refs: any = this.$refs;
    for (const i in refs) {
      if (refs[i].length > 0) {
        refs[i][0].resizeChart();
      }
    }
    this.refreshGrid();
  }

  // 竖向扩展块
  handleExtendHeight(block: any, direction: any) {
    containerProxy.blockHeightExtend(block, direction);
    const refs: any = this.$refs;
    for (const i in refs) {
      if (refs[i].length > 0) {
        refs[i][0].resizeChart();
      }
    }
    this.refreshGrid();
  }

  // 横向回缩块
  handleShortenWidth(block: any, direction: any) {
    containerProxy.blockWidthShorten(block, direction);
    const refs: any = this.$refs;
    for (const i in refs) {
      if (refs[i].length > 0) {
        refs[i][0].resizeChart();
      }
    }
    this.refreshGrid();
  }

  // 纵向回缩块
  handleShortenHeight(block: any, direction: any) {
    containerProxy.blockHeightShorten(block, direction);
    const refs: any = this.$refs;
    for (const i in refs) {
      if (refs[i].length > 0) {
        refs[i][0].resizeChart();
      }
    }
    this.refreshGrid();
  }

  // 工具栏的展开与回缩
  handleSideBarOpen(side) {
    if (side === 'left') {
      this.leftBarOpen = !this.leftBarOpen;
    } else {
      this.rightBarOpen = !this.rightBarOpen;
    }
    const refs: any = this.$refs;
    for (const i in refs) {
      if (refs[i].length > 0) {
        refs[i][0].resizeChart();
      }
    }
  }

  // 字段节点拖拽
  fieldDragStart(obj: any) {
    this.draggingType = 'dataSet';
    this.dragNodeData = obj.node;
  }

  // 应用布局模板
  applyTemplate(layoutGrid: any) {
    const grid: any = cloneDeep(layoutGrid);
    this.ROWS = getGridROWS(grid);
    containerProxy = new CanvasContainer(grid);
    this.refreshGrid();
  }

  // 初始化页面模板
  initGrid() {
    containerProxy = new CanvasContainer();
    this.refreshGrid(true);
  }

  // 刷新模板
  refreshGrid(init?) {
    this.grid = containerProxy.getGrid();

    // init === true: 刚进入页面初始化模板时editFlag保持为false；其他情况刷新模板时editFlag设置为true，代表对页面有过操作
    if (!init) {
      this.handleEditFlagChange(true);
    }
  }

  // 修改模板内容
  changeGrid() {
    for (let i = 0, len = this.grid.length; i < len; i++) {
      const grid: any = this.grid[i];
      if (grid.id === this.currentBlock.id) {
        if (!grid.empty) {
          grid.goods = this.currentBlock.goods;
        }
      }
    }
    containerProxy = new CanvasContainer(this.grid);
    this.refreshGrid();
  }

  // 当前块表单项数据处理
  setFormItem(formItemType: any, block: any) {
    if (
      block.goods &&
      block.goods.type &&
      (block.goods.type === 'singleForm' || block.goods.type === 'doubleForm')
    ) {
      const formItem: any = {
        type: block.goods.type, // 所在块是单列表单还是双列表单
        formItemType,
        key: Math.random()
          .toString(36)
          .substring(2),
        span: block.goods.type === 'singleForm' ? 24 : 12
      };

      const placeholder =
        ['text', 'textarea'].indexOf(formItemType) > -1 ? '请输入' : '请选择';
      const defaultOptions = [
        {
          value: Math.random()
            .toString(36)
            .substring(2),
          label: '选项1'
        },
        {
          value: Math.random()
            .toString(36)
            .substring(2),
          label: '选项2'
        },
        {
          value: Math.random()
            .toString(36)
            .substring(2),
          label: '选项3'
        }
      ];

      switch (formItemType) {
        case 'text':
          formItem.label = '单行文本';
          formItem.placeholder = placeholder;
          formItem.value = '';
          break;
        case 'textarea':
          formItem.label = '多行文本';
          formItem.placeholder = placeholder;
          formItem.value = '';
          break;
        case 'select':
          formItem.label = '下拉框';
          formItem.placeholder = placeholder;
          formItem.value = '';
          formItem.multiple = false;
          formItem.options = defaultOptions;
          break;
        case 'date':
          formItem.label = '日期';
          formItem.placeholder = placeholder;
          formItem.value = '';
          break;
        case 'time':
          formItem.label = '时间';
          formItem.placeholder = placeholder;
          formItem.value = '';
          break;
        case 'radioGroup':
          formItem.label = '单选框组';
          formItem.value = '';
          formItem.options = defaultOptions;
          break;
        case 'checkboxGroup':
          formItem.label = '复选框组';
          formItem.value = [];
          formItem.options = defaultOptions;
          break;
        case 'upload':
          formItem.btnProp = {
            buttonText: '文件上传'
          };
          formItem.iconProps = {
            iconClass: 'upload-icon'
          };
          break;
        case 'button':
          formItem.btnProp = {
            buttonText: '确定'
          };
          break;
        default:
      }

      if (formItem.formItemType) {
        this.activeFormItem = formItem;
        block.goods.formAttribute.formList.push(formItem);
      }
      containerProxy.setDraggingBlockContent(block);
      this.refreshGrid();
    } else {
      this.$message.warning('请将表单元素拖入到表单内');
    }
  }

  // 更新当前高亮表单项信息
  activeFormItemChange(val: any) {
    this.activeFormItem = val;
    if (this.currentBlock.goods && this.currentBlock.goods.formAttribute) {
      const formList = this.currentBlock.goods.formAttribute.formList;
      formList.forEach((item, index, arr) => {
        if (item.key === val.key) {
          this.$set(arr, index, val);
        }
      });
    }
    this.updateActiveBlock(cloneDeep(this.currentBlock));
  }

  // 根据id回显模板详情
  displayTemplateDetail() {
    const { taskForm } = this;
    if (JSON.stringify(taskForm) !== '{}') {
      this.loading = true;
      // this.templateName =
      //   this.$route.query.type === 'modify'
      //     ? taskForm.name
      //     : taskForm.name + '-副本';

      const grid = JSON.parse(Base64.decode(taskForm.style));
      const blocks = taskForm.blocks;
      for (let i = 0; i < grid.length; i++) {
        const base = grid[i];
        const item = arrayFind(blocks, 'blockId', base.goods);
        if (!base.empty) {
          this.switchBlock(item, base);
          this.getRealContentById(base);
        } else {
          base.goods = {};
        }
      }
      this.grid = cloneDeep(grid);
      this.ROWS = getGridROWS(grid);
      containerProxy = new CanvasContainer(grid);
      this.refreshGrid(true);
      this.loading = false;
    }
  }

  // 模块匹配，根据模块的类别进行不同的适配
  switchBlock(item: any, base: any) {
    base.goods = {
      blockName: item.blockName,
      showBlockName: item.showBlockName || false,
      queryParams: item.queryParams || [],
      relationQuery: item.relationQuery || []
    };
    if (item.dataSetId) {
      base.goods.dataSetId = item.dataSetId;
      const dataset = cloneDeep(
        arrayFind(this.dataSetList, 'id', item.dataSetId)
      );
      if (dataset && dataset.fields) {
        const fields = dataset.fields;
        if (item.type === 'table') {
          base.goods.type = 'table';
          // 表格，模拟循环拖入所有列字段
          item.columns.forEach((innerItem: any) => {
            const col = arrayFind(fields, 'fieldCode', innerItem.fieldCode);
            if (col) {
              col.align = innerItem.align;
              col.format = innerItem.format;
              col.canSet = innerItem.canSet;
              changeBlockFields(col, base);
            }
          });
        } else if (item.type === 'pie') {
          base.goods.type = 'pie';
          // 饼图，模拟拖入label和value
          const startLabelNode = arrayFind(fields, 'fieldCode', item.label);
          if (startLabelNode) {
            startLabelNode.attrFlag = 'pieLabel';
            startLabelNode.pieType = item.pieType;
            changeBlockFields(startLabelNode, base);
          }
          const startValueNode = arrayFind(fields, 'fieldCode', item.value);
          if (startValueNode) {
            startValueNode.attrFlag = 'pieValue';
            startValueNode.pieType = item.pieType;
            changeBlockFields(startValueNode, base);
          }
        } else if (item.type === 'line') {
          base.goods.type = 'line';
          // 折线图，模拟拖入x轴和所有的y轴
          const startXaxisNode = arrayFind(fields, 'fieldCode', item.xaxis);
          if (startXaxisNode) {
            startXaxisNode.attrFlag = 'xaxis';
            changeBlockFields(startXaxisNode, base);
          }
          item.yaxis.forEach((ys: any) => {
            const startYaxisNode = arrayFind(fields, 'fieldCode', ys.code);
            if (startYaxisNode) {
              startYaxisNode.attrFlag = 'yaxis';
              changeBlockFields(startYaxisNode, base);
            }
          });
        } else if (item.type === 'bar') {
          base.goods.type = 'bar';
          // 柱图，模拟拖入x轴和所有的y轴
          const startXaxisNode = arrayFind(fields, 'fieldCode', item.xaxis);
          if (startXaxisNode) {
            startXaxisNode.attrFlag = 'xaxis';
            changeBlockFields(startXaxisNode, base);
          }
          item.yaxis.forEach((ys: any) => {
            const startYaxisNode = arrayFind(fields, 'fieldCode', ys.code);
            if (startYaxisNode) {
              startYaxisNode.attrFlag = 'yaxis';
              changeBlockFields(startYaxisNode, base);
            }
          });
        } else if (item.type === 'monitorSteps') {
          base.goods.type = 'monitorSteps';
          base.goods.stepCount = item.stepCount;
          base.goods.stepsList = item.stepsList;
        } else if (item.type === 'monitorChart') {
          base.goods.type = 'monitorChart';
          base.goods.allCount = item.allCount;
          base.goods.completeCount = item.completeCount;
          base.goods.monitorColor = item.monitorColor;
          base.goods.monitorName = item.monitorName;
        }
      }
    }

    if (item.formAttribute) {
      base.goods.formAttribute = item.formAttribute;
      this.formAttribute = item.formAttribute;
      base.goods.type = item.type;
    }
  }

  // 将字段拖动到监控视图中
  handleMonitorDrop(block: any, type: any) {
    if (block.goods.monitorName) {
      if (this.draggingType === 'dataSet') {
        const dragNode = this.dragNodeData;
        let dataSetId = '';
        if (!dragNode.isLeaf) {
          dataSetId = dragNode.data.id;
          this.$message.error('请拖入单一字段');
        } else {
          dataSetId = dragNode.parent.data.id;
          dragNode.data.attrFlag = type;
        }
        // block.goods.dataSetId = dataSetId;
        this.drawCanvas(block, dataSetId, dragNode);
      }
    } else {
      this.$message.error('请先在图表属性中配置监控名称');
    }
    this.switchActiveBlock(block);
  }

  // 根据关联图表或表单项查询数据
  queryRelationDataset(info: any) {
    this.grid.forEach((item) => {
      if (item.goods && item.goods.queryParams) {
        let query = false;
        item.goods.queryParams.forEach((innerItem) => {
          // 点击图表某一区域触发与之相关联图表的查询
          if (info.type === 'chart') {
            if (
              innerItem.valueBy === '3' &&
              innerItem.relationChart === info.relationBlock
            ) {
              query = true;
              this.$set(innerItem, 'value', info[innerItem.relationChartField]);
            }
          }

          // 点击表单块中的查询按钮触发与表单项关联的图表数据查询
          if (info.type === 'formItem') {
            if (innerItem.valueBy === '2') {
              for (let i = 0, len = info.formItems.length; i < len; i++) {
                const formItem = info.formItems[i];
                if (innerItem.relationFormItem === formItem.formItemKey) {
                  query = true;
                  this.$set(innerItem, 'value', formItem.formItemValue);
                  break;
                }
              }
            }
          }
        });
        if (query) {
          this.getRealContentById(item);
        }
      }
    });
  }

  // 监控卡片关联表格数据
  getDataByCard(info) {
    this.grid.forEach((block) => {
      if (
        block.goods &&
        block.goods.type === 'table' &&
        block.goods.dataSetId
      ) {
        const queryParams = block.goods.queryParams.filter((item: any) => {
          return item.name === '监控名称';
        })[0];
        const params = {
          viewId: block.goods.dataSetId,
          queryParams: queryParams
            ? [
                {
                  code: queryParams.code,
                  type: queryParams.type,
                  value: info.monitorName
                }
              ]
            : []
        };
        dataViewQuery(params).then(
          ({ data, error }: { data: any; error: any }) => {
            if (!error) {
              data.rows.forEach((item: any) => {
                item.isEdit = true;
              });
              this.$set(this.multipleData, block.id, data.rows);
            } else {
              this.$message.error(error.message || '获取数据失败');
            }
          }
        );
      }
    });
  }

  created() {
    this.initGrid();
    this.getDataSet();
    this.loading = !!this.$route.query.id;
    this.getDataSets().then(() => {
      this.displayTemplateDetail();
    });
    this.getLayoutTemplateList();
  }
}
