










































































import { Component, Vue, Prop, Emit } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import { cloneDeep } from 'lodash';
import { getFormatter } from '../utils/util';
import vuedraggable from 'vuedraggable';
const pageTemplate = namespace('pageTemplate');

@Component({
  components: { vuedraggable }
})
export default class TableConfig extends Vue {
  @pageTemplate.State((state) => state.dataSetList) dataSetList: Array<any>;
  @pageTemplate.State((state) => state.activeBlock) activeBlock: any;
  @pageTemplate.Mutation('UPDATE_ACTIVE_BLOCK') updateActiveBlock;

  @Emit('changeGrid') changeGrid() {}
  @Prop() configForm: any;

  get currentBlock() {
    return cloneDeep(this.activeBlock);
  }

  get config() {
    return cloneDeep(this.configForm);
  }

  // 改变表格字段顺序
  handleOrderChange() {
    this.currentBlock.goods.columns = this.config.tableFields;
    this.updateActiveBlock(cloneDeep(this.currentBlock));
    this.changeGrid();
  }

  // 删除表格字段
  deleteTableColumn(field: any) {
    if (this.currentBlock.goods && this.currentBlock.goods.columns) {
      const index = this.currentBlock.goods.columns.findIndex(
        (item: any) => item.fieldCode === field.fieldCode
      );
      this.currentBlock.goods.columns.splice(index, 1);
      this.configForm.tableFields.splice(index, 1);
      if (!this.currentBlock.goods.columns.length) {
        this.currentBlock.goods.dataSetId = '';
      }
      this.updateActiveBlock(cloneDeep(this.currentBlock));
      this.changeGrid();
    }
  }

  // 改变表格列对齐方式
  handleAlignChange(val: any, field: any) {
    if (this.currentBlock.goods && this.currentBlock.goods.columns) {
      this.currentBlock.goods.columns.map((item: any) => {
        if (item.fieldCode === field.fieldCode) {
          item.align = val;
        }
      });
      this.updateActiveBlock(cloneDeep(this.currentBlock));
      this.changeGrid();
    }
  }

  // 改变表格格式化方式
  handleFormatChange(val: any, field: any) {
    if (this.currentBlock.goods && this.currentBlock.goods.columns) {
      this.currentBlock.goods.columns.map((item: any) => {
        if (item.fieldCode === field.fieldCode) {
          this.$set(item, 'format', val);
          this.$set(item, 'formatter', getFormatter(val));
        }
      });
      this.updateActiveBlock(cloneDeep(this.currentBlock));
      this.changeGrid();
    }
  }

  // 改变表格列是否可编辑
  handleEditChange(val: any, field: any) {
    if (this.currentBlock.goods && this.currentBlock.goods.columns) {
      const currentBlock = cloneDeep(this.currentBlock);
      currentBlock.goods.columns.map((item: any) => {
        if (item.fieldCode === field.fieldCode) {
          if (val) {
            item.canSet = 1;
          } else {
            item.canSet = 0;
          }
          this.updateActiveBlock(currentBlock);
          this.changeGrid();
        }
      });
    }
  }
}
