















































































































import { Component, Vue, Prop, Emit } from 'vue-property-decorator';
import { cloneDeep } from 'lodash';
import { namespace } from 'vuex-class';
import { arrayFind } from '@utils/util';
const pageTemplate = namespace('pageTemplate');

@Component
export default class MonitorStepsConfig extends Vue {
  @Prop() configForm: any;
  @Emit('activeFormItemChange') updateActiveFormItem(item: any) {}
  @Emit('changeGrid') changeGrid() {}
  @Emit('getRealContentById') getRealContentById(currentBlock) {}

  @pageTemplate.State((state) => state.activeBlock) activeBlock: any;
  @pageTemplate.State((state) => state.dataSetList) dataSetList: Array<any>;
  @pageTemplate.Mutation('UPDATE_ACTIVE_BLOCK') updateActiveBlock;

  get config() {
    const config = cloneDeep(this.configForm);
    if (config.stepsList) {
      config.stepsList.forEach((item: any) => {
        if (item.dataSetId) {
          item.options = cloneDeep(
            arrayFind(this.dataSetList, 'id', item.dataSetId)
          ).fields;
        }
      });
    }
    return config;
  }
  get selectList() {
    const steps = this.configForm.stepsList;
    const list: any = [];
    steps.forEach((item: any, index: any) => {
      const obj = {
        id: index + 1,
        label: '节点' + (index + 1)
      };
      list.push(obj);
    });
    return list;
  }
  handleStepChange(value: any, type: any, index: any) {
    const activeBlock = cloneDeep(this.activeBlock);
    if (type === 'stepCount') {
      this.config.stepCount = value;
      let i = 0;
      if (this.config.stepsList) {
        i = this.config.stepsList.length;
      }
      if (value > i) {
        for (i; i < parseInt(value); i++) {
          const obj = {
            monitorName: '',
            completeCount: '',
            allCount: '',
            options: i > 0 ? this.config.stepsList[i - 1].options : '',
            dataSetId: i > 0 ? this.config.stepsList[i - 1].dataSetId : ''
          };
          this.config.stepsList.push(obj);
        }
      } else {
        for (value; value < i; i--) {
          this.config.stepsList.pop();
          this.config.currentStep = '';
          activeBlock.goods.currentStep = '';
        }
      }
      activeBlock.goods.stepsList = this.config.stepsList;
      activeBlock.goods.stepCount = value;
      activeBlock.goods['currentStep'] = this.config.stepsList.length;
    } else if (type === 'currentStep') {
      this.config[type] = value;
      activeBlock.goods[type] = value;
    } else {
      const current = this.config.stepsList[index];
      if (type === 'monitorName') {
        current[type] = value;
        activeBlock.goods['currentStep'] = index + 1;
        this.$set(this.config.stepsList, index, current);
        this.$set(activeBlock.goods.stepsList, index, current);
      } else {
        if (current.monitorName) {
          current[type] = value;
          activeBlock.goods['queryName'] = current.monitorName;
          // activeBlock.goods['currentStep'] = index;
          this.$set(this.config.stepsList, index, current);
          this.$set(activeBlock.goods.stepsList, index, current);
          this.getRealContentById(activeBlock);
        } else {
          current[type] = '';
          this.$message.error('请先输入当前节点监控名称');
        }
      }
    }
    this.updateActiveBlock(activeBlock);
    this.updateActiveFormItem(this.config);
    this.changeGrid();
  }
}
