




































import { Component, Vue, Prop, Emit } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import { cloneDeep } from 'lodash';
import { arrayFind } from '@utils/util';
const pageTemplate = namespace('pageTemplate');

@Component
export default class BarAndLineConfig extends Vue {
  @pageTemplate.State((state) => state.dataSetList) dataSetList: Array<any>;
  @pageTemplate.State((state) => state.activeBlock) activeBlock: any;
  @pageTemplate.Mutation('UPDATE_ACTIVE_BLOCK') updateActiveBlock;

  @Emit('changeGrid') changeGrid() {}
  @Prop() configForm: any;

  get currentBlock() {
    return cloneDeep(this.activeBlock);
  }

  get config() {
    const { type, blockName } = this.configForm;
    const xaxis =
      type === 'bar' ? this.configForm.barXaxis : this.configForm.lineXaxis;
    const yaxis =
      type === 'bar' ? this.configForm.barYaxis : this.configForm.lineYaxis;
    const xaxisOptions =
      type === 'bar'
        ? this.configForm.barXaxisOptions
        : this.configForm.lineXaxisOptions;
    const yaxisOptions =
      type === 'bar'
        ? this.configForm.barYaxisOptions
        : this.configForm.lineYaxisOptions;

    return {
      type,
      blockName,
      xaxis,
      yaxis,
      xaxisOptions,
      yaxisOptions
    };
  }

  // 柱状图和折线图属性修改
  handleConfigFormChange(val: any, attr: any) {
    if (attr === 'yaxis') {
      const dataset = cloneDeep(
        arrayFind(this.dataSetList, 'id', this.currentBlock.goods.dataSetId)
      );
      const yaxis: Array<any> = [];
      val.map((item: any) => {
        const field = dataset.fields.find(
          (innerItem: any) => innerItem.fieldCode === item
        );
        if (field) {
          yaxis.push({
            code: field.fieldCode,
            label: field.fieldName
          });
        }
      });
      this.currentBlock.goods.yaxis = yaxis;
    } else {
      this.currentBlock.goods.xaxis = val;
    }
    this.updateActiveBlock(cloneDeep(this.currentBlock));
    this.changeGrid();
  }
}
