











































import { Component, Vue, Emit } from 'vue-property-decorator';
import Base64 from '@utils/Base64';
import { deleteLayoutTemplate } from '@api/pageTemplate';
import { namespace } from 'vuex-class';
import { getGridROWS, getGridCOLUMNS } from '../utils/util';
const pageTemplate = namespace('pageTemplate');

@Component
export default class LayoutTemplate extends Vue {
  @pageTemplate.State((state) => state.layoutList) layoutList!: Array<any>;
  @pageTemplate.Action('getLayoutTemplateList') getLayoutTemplateList;
  @Emit('applyTemplate') apply(layoutTemplate: Array<any>) {}

  private dialogVisible = false;
  private activeLayout: any = null;

  get layoutGrid() {
    return this.layoutList.map((item: any) => {
      const grid = JSON.parse(Base64.decode(item.style));
      return {
        id: item.id,
        grid,
        COLUMNS: getGridCOLUMNS(grid),
        ROWS: getGridROWS(grid)
      };
    });
  }

  cancelDialog() {
    this.dialogVisible = false;
    this.activeLayout = null;
  }

  // 打开预览弹窗
  openPreviewDialog() {
    if (!this.layoutList.length) {
      this.$message.warning('您还没有创建布局模板');
    } else {
      this.dialogVisible = true;
    }
  }

  // 删除模板
  deleteLayout(id: any) {
    this.$confirm('是否确认删除?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    })
      .then(() => {
        const param = { id };
        deleteLayoutTemplate(param).then(({ error }: any) => {
          if (!error) {
            this.$message.success('删除成功');
            this.getLayoutTemplateList();
          } else {
            this.$message.warning(error.message);
          }
        });
      })
      .catch(() => {});
  }

  // 应用模板
  applyLayout() {
    if (!this.activeLayout) {
      this.cancelDialog();
      return;
    }
    let layoutTemp: any = this.layoutGrid.find(
      (item: any) => item.id === this.activeLayout
    );
    layoutTemp = layoutTemp && layoutTemp.grid;
    this.apply(layoutTemp);
    this.cancelDialog();
  }

  // 选中模板
  selectLayout(id: any) {
    this.activeLayout = id;
  }
}
