









































































































































































































































import { Component, Vue, Prop, Emit, Watch } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import { arrayFind } from '@utils/util';
import { cloneDeep } from 'lodash';
import TableConfig from './tableConfig.vue';
import RightSet from '@views/processConfig/customForm/edit/rightSet.vue';
import PieConfig from './pieConfig.vue';
import FormConfig from './formConfig.vue';
import BarAndLineConfig from './barAndLineConfig.vue';
import MonitorConfig from './monitorConfig.vue';
import MonitorStepsConfig from './monitorStepsConfig.vue';
import { queryCustom } from '@api/customColumn';
const pageTemplate = namespace('pageTemplate');

@Component({
  components: {
    TableConfig,
    RightSet,
    PieConfig,
    BarAndLineConfig,
    FormConfig,
    MonitorConfig,
    MonitorStepsConfig
  }
})
export default class RightToolbar extends Vue {
  @pageTemplate.State((state) => state.dataSetList) dataSetList: Array<any>;
  @pageTemplate.State((state) => state.activeBlock) activeBlock: any;
  @pageTemplate.Mutation('UPDATE_EDIT_FLAG') handleEditFlagChange;
  @pageTemplate.Mutation('UPDATE_ACTIVE_BLOCK') updateActiveBlock;

  @Prop({ default: {} }) itemAttribute: any;
  @Prop() formAttribute: any;
  @Prop() dataSet: any;
  @Prop() viewDataSet: any;
  @Prop() optionLabel: any;
  @Prop() grid: any;

  @Emit('fieldDragStart')
  fieldDragStart(node: any) {}
  @Emit('changeGrid') changeGrid() {}
  @Emit('getRealContentById') getRealContentById(currentBlock) {}
  @Emit('handleSideBarOpen') handleSideBarOpen(direction) {}

  @Watch('clickRandom')
  clickBlock() {
    if (this.currentBlock.goods.dataSetId) {
      this.selectedDataSet = this.currentBlock.goods.dataSetId;
    } else {
      this.selectedDataSet = null;
    }
  }
  @Watch('currentBlock')
  handleCurrentBlockChange() {
    if (!this.currentBlock.empty && this.currentBlock.goods) {
      this.configForm = {
        blockName: this.currentBlock.goods.blockName || '',
        showBlockName: !!this.currentBlock.goods.showBlockName
      };

      // 当前数据视图
      if (this.currentBlock.goods.dataSetId) {
        this.selectedDataSet = this.currentBlock.goods.dataSetId;
        const dataset = cloneDeep(
          arrayFind(this.dataSetList, 'id', this.currentBlock.goods.dataSetId)
        );
        // 获取图表属性配置
        if (dataset && dataset.fields) {
          switch (this.currentBlock.goods.type) {
            case 'table':
              this.changeTableConfig();
              break;
            case 'pie':
              this.changePieConfig(dataset);
              break;
            case 'line':
              this.changeLineConfig(dataset);
              break;
            case 'bar':
              this.changeBarConfig(dataset);
              break;
            default:
              break;
          }
        }
      }

      // 表单配置
      if (
        this.currentBlock.goods.type === 'singleForm' ||
        this.currentBlock.goods.type === 'doubleForm'
      ) {
        this.changeFormConfig();
      }

      // 监控图标配置
      if (this.currentBlock.goods.type === 'monitorChart') {
        this.changeMonitorConfig();
      }
      // 监控节点配置
      if (this.currentBlock.goods.type === 'monitorSteps') {
        this.changeMonitorStepsConfig();
      }
    } else {
      this.configForm = {};
    }
  }
  @Watch('activeFormItem')
  handleActiveFormItem() {
    if (this.currentBlock && this.currentBlock.goods) {
      this.changeFormConfig();
    }
  }

  @Prop() hidden: boolean;
  @Prop() clickRandom: number;
  @Prop({ default: {} }) activeFormItem: any;
  @Prop() formItemList: any;
  @Prop() chartList: any;
  @Prop() rightBarOpen;

  private configForm: any = {}; // 图表属性的配置信息
  private selectedDataSet: number | null = null; // 选择的数据视图
  private selectedMonitorDataSet: number | null = null; // 选择的监控视图
  timer: any = {};

  get currentBlock() {
    return cloneDeep(this.activeBlock);
  }

  get fieldsTree() {
    const dataset = cloneDeep(
      arrayFind(this.dataSetList, 'id', this.selectedDataSet)
    );
    return dataset
      ? [
          {
            id: this.selectedDataSet,
            name: dataset.name,
            fields: dataset.fields.map((item: any) => ({
              ...item,
              name: item.fieldName
            }))
          }
        ]
      : [];
  }

  get queryParams() {
    return (
      (this.currentBlock.goods && this.currentBlock.goods.queryParams) || []
    );
  }

  get componentkey() {
    let result = '';
    switch (this.configForm.type) {
      case 'table':
        result = 'TableConfig';
        break;
      case 'pie':
        result = 'PieConfig';
        break;
      case 'line':
      case 'bar':
        result = 'BarAndLineConfig';
        break;
      case 'singleForm':
      case 'doubleForm':
        result = 'FormConfig';
        break;
      case 'monitorChart':
        result = 'MonitorConfig';
        break;
      case 'monitorSteps':
        result = 'MonitorStepsConfig';
        break;
      default:
    }
    return result;
  }

  getRelationChartOptions(selectedChart) {
    const select = this.chartList.find((item) => item.value === selectedChart);
    return select ? select.options : [];
  }

  // 修改块名称
  handleBaseInfoChange(attr: string, value: any) {
    if (this.currentBlock.goods) {
      this.$set(this.currentBlock.goods, attr, value);
      console.log('123W', this.currentBlock);
      this.updateActiveBlock(cloneDeep(this.currentBlock));
      this.changeGrid();
      this.handleEditFlagChange(true);
    }
  }

  // 当前块为表格的右侧模板信息
  changeTableConfig() {
    const columns = this.currentBlock.goods.columns;
    const value = cloneDeep(columns);
    this.configForm = {
      type: 'table',
      blockName: this.currentBlock.goods.blockName || '',
      tableFields: value || [],
      showBlockName: !!this.currentBlock.goods.showBlockName
    };
  }

  // 当前块为饼图的右侧模板信息
  changePieConfig(dataset: any) {
    const {
      blockName,
      pieType,
      label,
      value,
      showBlockName
    } = this.currentBlock.goods;
    this.configForm = {
      type: 'pie',
      blockName: blockName || '',
      pieType: pieType || '0',
      pieLabel: label,
      pieLabelOptions: dataset.fields,
      pieValue: value,
      pieValueOptions: dataset.fields,
      showBlockName: !!showBlockName
    };
  }

  // 当前块为折线图的右侧模板信息
  changeLineConfig(dataset: any) {
    const { blockName, xaxis, yaxis, showBlockName } = this.currentBlock.goods;
    let yAxis: Array<any> = [];
    if (yaxis) {
      yAxis = yaxis.map((item) => item.code);
    }
    this.configForm = {
      type: 'line',
      blockName: blockName || '',
      lineXaxis: xaxis,
      lineXaxisOptions: dataset.fields,
      lineYaxis: yAxis,
      lineYaxisOptions: dataset.fields,
      showBlockName: showBlockName
    };
  }

  // 当前块为柱图的右侧模板信息
  changeBarConfig(dataset: any) {
    const { blockName, xaxis, yaxis, showBlockName } = this.currentBlock.goods;
    let yAxis: Array<any> = [];
    if (yaxis) {
      yAxis = yaxis.map((item) => item.code);
    }

    this.configForm = {
      type: 'bar',
      blockName: blockName || '',
      barXaxis: xaxis,
      barXaxisOptions: dataset.fields,
      barYaxis: yAxis,
      barYaxisOptions: dataset.fields,
      showBlockName: showBlockName
    };
  }

  // 当前块为表单的右侧属性配置
  changeFormConfig() {
    this.configForm = {
      blockName: this.currentBlock.goods.blockName || '',
      ...this.activeFormItem,
      showBlockName: !!this.currentBlock.goods.showBlockName
    };
  }

  // 当前块为监控视图
  changeMonitorConfig() {
    const {
      monitorName,
      blockName,
      showBlockName,
      allCount,
      completeCount,
      monitorColor
    } = this.currentBlock.goods;
    this.configForm = {
      type: 'monitorChart',
      allCount: allCount || '',
      completeCount: completeCount || '',
      monitorName: monitorName || '',
      options: this.fieldsTree.length ? this.fieldsTree[0].fields : [],
      showBlockName: showBlockName,
      blockName: blockName || '',
      monitorColor: monitorColor || '#409eff'
    };
    this.selectedMonitorDataSet = monitorName || '';
  }

  // 当前块为监控节点
  changeMonitorStepsConfig() {
    const {
      stepsList,
      stepCount,
      blockName,
      showBlockName,
      dataSetId,
      currentStep
    } = this.currentBlock.goods;
    this.configForm = {
      type: 'monitorSteps',
      currentStep: currentStep ? currentStep : '',
      stepsList: stepsList || [],
      stepCount: stepCount || 0,
      // options: this.fieldsTree.length ? this.fieldsTree[0].fields : [],
      blockName: blockName || '',
      showBlockName: showBlockName,
      dataSetId: dataSetId || ''
    };
  }

  // 参数值获取方式
  handleParamsChange(attr: string, val: any, item?: any) {
    if (attr === 'valueBy') {
      // 切换值获取方式时重置value
      item.value = item.defaultValue || '';
    }
    if (attr === 'value') {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        this.getRealContentById(this.currentBlock);
      }, 1000);
    }

    this.updateActiveBlock(cloneDeep(this.currentBlock));
    this.changeGrid();
  }
  //
  handleViweChange(e) {
    if (
      this.currentBlock.goods &&
      this.currentBlock.goods.type === 'monitorChart'
    ) {
      this.currentBlock.goods.dataSetId = e;
      this.updateActiveBlock(cloneDeep(this.currentBlock));
      this.changeGrid();
    }
    if (
      this.currentBlock.goods &&
      this.currentBlock.goods.type === 'monitorSteps'
    ) {
      this.currentBlock.goods.dataSetId = e;
      if (this.currentBlock.goods.stepCount) {
        this.currentBlock.goods.stepsList[
          this.currentBlock.goods.currentStep - 1
        ]['dataSetId'] = e;
        this.currentBlock.goods.stepsList[
          this.currentBlock.goods.currentStep - 1
        ]['completeCount'] = '';
        this.currentBlock.goods.stepsList[
          this.currentBlock.goods.currentStep - 1
        ]['allCount'] = '';
        // this.currentBlock.goods.stepsList[this.currentBlock.goods.currentStep]['options'] = this.fieldsTree.length ? this.fieldsTree[0].fields : [],
        this.updateActiveBlock(cloneDeep(this.currentBlock));
        this.changeGrid();
      } else {
        this.selectedDataSet = null;
        this.$message.error('请先在图表属性中设置节点个数');
      }
    }
  }
}
