





























































































































































import { Component, Vue, Prop, Emit } from 'vue-property-decorator';
import { cloneDeep } from 'lodash';
import { namespace } from 'vuex-class';
const pageTemplate = namespace('pageTemplate');

@Component
export default class FormConfig extends Vue {
  @pageTemplate.Mutation('UPDATE_ACTIVE_BLOCK') updateActiveBlock;
  @pageTemplate.State((state) => state.activeBlock) activeBlock: any;

  @Prop() configForm: any;

  @Emit('changeGrid') changeGrid() {}
  @Emit('activeFormItemChange') updateActiveFormItem(item: any) {}

  get config() {
    return cloneDeep(this.configForm);
  }

  handleFormConfigChange(attr: any, val: any) {
    const activeBlock = cloneDeep(this.activeBlock);
    const formList = activeBlock.goods.formAttribute.formList;
    formList.forEach((item) => {
      if (item.key === this.config.key) {
        if (attr.indexOf('.') > -1) {
          const attrs = attr.split('.');
          item[attrs[0]][attrs[1]] = val;
        } else {
          item[attr] = val;
        }
      }
    });
    this.updateActiveFormItem(this.config);
    this.updateActiveBlock(activeBlock);
    this.changeGrid();
  }

  // 表单项静态数据删除操作
  deleteTabRow(index: any, rows: any) {
    // 如果删除的项在默认值中已选中，则将选中项删除
    const deleteItem =
      this.config.formItemType === 'checkboxGroup'
        ? rows[index].label
        : rows[index].value;
    if (Array.isArray(this.config.value)) {
      this.config.value.forEach((item, i, arr) => {
        if (item === deleteItem) {
          arr.splice(i, 1);
        }
      });
    } else {
      if (deleteItem === this.config.value) {
        this.config.value = '';
      }
    }

    rows.splice(index, 1);
    const activeBlock = cloneDeep(this.activeBlock);
    const formList = activeBlock.goods.formAttribute.formList;
    formList.forEach((item) => {
      if (item.key === this.config.key) {
        item.options.splice(index, 1);

        // 如果删除的项在内容块中被选中，也要删除该选中的值
        if (Array.isArray(item.value)) {
          item.value.forEach((item, i, arr) => {
            if (item === deleteItem) {
              arr.splice(i, 1);
            }
          });
        } else {
          if (deleteItem === item.value) {
            item.value = '';
          }
        }
      }
    });
    this.updateActiveFormItem(this.config);
    this.updateActiveBlock(activeBlock);
    this.changeGrid();
  }

  handelOptionsChange(val, $index) {
    const activeBlock = cloneDeep(this.activeBlock);
    const formList = activeBlock.goods.formAttribute.formList;
    formList.forEach((item) => {
      if (item.key === this.config.key) {
        item.options[$index].label = val;
      }
    });
    this.updateActiveFormItem(this.config);
    this.updateActiveBlock(activeBlock);
    this.changeGrid();
  }

  // 表单项静态数据添加
  addTabRow(rows: any) {
    const activeBlock = cloneDeep(this.activeBlock);
    const formList = activeBlock.goods.formAttribute.formList;
    const obj = {
      label: '选项' + (this.config.options.length + 1),
      value: Math.random()
        .toString(36)
        .substring(2)
    };
    formList.forEach((item) => {
      if (item.key === this.config.key) {
        item.options.push(obj);
      }
    });
    rows.push(obj);
    this.updateActiveFormItem(this.config);
    this.updateActiveBlock(activeBlock);
    this.changeGrid();
  }
}
